import React, { useEffect } from 'react';
import { AppState, appStateDefault } from './contexts';

function App() {
  const [state, setState] = React.useState(appStateDefault);

  useEffect(() => {
    setState(c => ({
      ...c,
      test_value: 'test2'
    }));
  }, []);

  return (
    <AppState.Provider value={Object.assign({}, state, {      
    })}>
      <React.Fragment>
        <div>
          {JSON.stringify(state)}
        </div>
        <div>
        </div>
      </React.Fragment>
    </AppState.Provider>
  );
}

export default App;
